.app {
  text-align: center;
}

/* HEADER */
.app-header {
  background-color: #3c4049;
  min-height: 50px;
  font-size: 20px;
  color: white;
  box-shadow: 0px 2.5px #0000001a;
}

#logo{
  float: left;
  margin-top: 10px;
  margin-left: 10px;
}

.main-nav{
  float: right;
  color: #61dafb;
  margin-top: 10px;
  margin-right: 10px;
}

.main-nav a{
  display: inline-block;
  padding-left: 15px;
}

.main-nav a:hover{
  display: inline-block;
  color: white;
  cursor: pointer;
  padding-left: 15px;
}

/* For mobile phones: */
@media only screen and (max-width: 768px) {
  .main-nav{
    display: none;
  }

  .app-header{
    justify-content: center;
  }

  #logo{
    float: none;
    display: inline-block;
    align-self: center;
  }
}

/* BODY */

body{
  background-color: #282c34;
}